$BlueMode-Question-color: #57A6B4;
$BlueMode-Question-background: #FEF2F2;

//BrownMode
$brownMode-Back-color: #E3DAD2;
$brownMode-Text-color: #374151;
$brownMode-Title-color: #4C3932;
$brownMode-Disc-color: #374151;
$brownMode-Back2-color: #4C3932;
$brownMode-Question-background: #FEF2F2;
$brownMode-Question-color: #C16746;

.redMode{
    .Audio{
        display: flex;
        height: 100px;
        width: 100%;
        border-radius: 16px;
        background-color: $RedMode-Title-color;
        padding: 16px;
    
        &__inner{
            display: flex;
            height: 100%;
            width: 100%;
            align-items: center;
            justify-content: space-between;
    
            &__image{
                height: 100%;
                width: 25%;
                border-radius: 12px;
                background-position: center;
                background-size: cover;
            }
            &__text{
                color: white;
                width: 60%;
                white-space: nowrap; /* Khng cho php xung dng */
                overflow: hidden; /* n ni dung tha nu qu di */
                text-overflow: ellipsis; /* Thm du "..." nu ni dung b ct */
                &__title{
                    font-weight: 700;
                    font-size: 20px;
                }
                &__disc{
                    font-weight: 400;
                }
            }
            &__play{
                padding: 10px;
                border-radius: 100px;
                background-color: white;
                font-size: 20px;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
            }
        }
    }
}

